.video__block {
  display: block;
  cursor: pointer;

  @media (min-width: @tablet-width) {
    width: 100%;
    height: 500px;
  }
}

.video {
  position: relative;
}

.video__button {
  position: absolute;
  content: "";
  width: 48px;
  height: 54px;
  background-color: transparent;
  background-image: url("../img/small-icons/triangle-button.svg");
  background-repeat: no-repeat;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
  border: none;
  padding: 0;
}
